*{
    box-sizing: border-box;
}

body{
    overflow: auto;
    overflow-y: scroll;
    overflow-x:hidden; 
}


.navigation {
    list-style-type: none;
    margin: 0 auto;
    margin-top: 2.5em;
    margin-bottom: 2.5em;
    padding: 0;
    text-align: center;
}

.navigation li {
    display: inline;
}

.navigation li a {
    color: #808e9b;
    text-align: center;
    padding: 2em;
    font-family: "Roboto";
    text-decoration: none;

}

.navigation li a:visited{
    color: #808e9b;
}

.container {
    display: block;
}

.intro{
    display: grid;
    grid-template-columns: 15% 45% 40%;
    grid-template-rows: 120px 50% 50;
    align-content: center;
}


.name{
    grid-row: 2;
    grid-column: 2;
}

#firstline {
    text-shadow: 1px 1px black;
    font-family: "Candara";
    color: #34495e;
    font-size: 3.5em;
}

#secondline {
    text-shadow: 1px 1px black;
    font-family: "Candara";
    color: #34495e;
    font-size: 2em;
}

.description{
    display: block;
    font-family: "Roboto";
    grid-column: 2;
    grid-row: 3/5;
    font-size: 1.1em;
    line-height: 2.1em;
}

.links{
    word-spacing: 20px;
    grid-row: 6;
    grid-column: 2;
    margin-top: 8%;
}

.links a{
    font-size: 0.9em;
    text-decoration: none;
    color: #000000;
    position: relative;
}

.links a::after{
    content: "";
    position: absolute;
    z-index: -1;
    top: 60%;
    left: -0.1em;
    right: -0.1em;
    bottom: 0;
    transition: top 200ms cubic-bezier(0, .8, .13, 1);
    background-color: #f7d794;
}


.links a:hover::after {
    top: 0;
}

.links a:visited{
    color: #000000;
}

footer{
    
    position: absolute;
    background-color: black;
    color: white;
    width: 100%;
    height: 100px;
    left: 0;
    bottom:-100px;
}

footer p{
    text-align: center;
    margin-top: 60px;
    font-size: 13px;
    font-family: sans-serif;
    color: #7f8fa6;
}

@media screen and (max-width:1024px){
    #background{
        width: 50%;
        margin-left: -512px;
    }
}